<template>
    <v-layout fluid>
        <v-dialog v-model="showSiteConnectionModal" @keydown.esc="$emit('close')" persistent width="1080px">
            <v-card>
                <v-row align-center justify-center style="margin-left: 15px">
                    <v-card-title>
                        <span class="headline">Connecting server</span>
                    </v-card-title>
                </v-row>
                <v-spacer/>
                <v-row align-center justify-center style="margin-left: 25px">
                    <v-btn @click=fillData()>Reload</v-btn>
                    <v-btn @click="interval_fun ? startInterval() : stopInterval()" style="margin-left: 10px">{{interval_btn}}</v-btn>
                    <v-btn @click="clearChart" style="margin-left: 10px">Clear</v-btn>
                    <v-btn @click="[stopInterval(), $emit('close')]" style="margin-left: 10px">Exit</v-btn>
                </v-row>
                <v-row align="center" justify-center style="margin-left: 15px">
                    <v-col cols="6">
                        <br/>
                        <span style="margin: 0"> Select server connection interval in seconds</span>
                        <v-slider
                            v-model="interval"
                            class="align-center"
                            :max="60"
                            :min="5"
                            hide-details
                        >
                            <template v-slot:append>
                                <v-text-field
                                    v-model="interval"
                                    class="mt-0 pt-0"
                                    :max="60"
                                    :min="5"
                                    hide-details
                                    single-line
                                    type="number"
                                    style="width: 43px"
                                ></v-text-field>
                            </template>
                        </v-slider>
                    </v-col>
                </v-row>
                <v-card-actions>
                    <v-row align="center" justify-center >
                        <v-col cols="8">
                            <div class="chartAreaWrapper">
                                <chart class="chart" :chart-data="datacollection" :options="options" ></chart>
                            </div>
                        </v-col>
                        <v-col cols="4">
                            <conn-table :table-data=this.data />
                        </v-col>
                    </v-row>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-layout>
</template>
<script>
    import Chart           from "./chart";
    import ConnTable       from "./conn-table";
    import settings        from "@/settings";
    import axios           from "axios";

    export default {
        name: "site-connect",
        components: { Chart, ConnTable },
        props:["site", "showSiteConnectionModal"],
        data() {
            return{
                site_params: {
                    id: this.site.id,
                    url: this.site.url,
                    ...this.site
                },
                interval_fun: true,
                interval_btn: 'Start Interval',
                interval: 5,
                $int: null,
                data: [],
                request_status: [],
                request_time: [],
                current_time: [],
                datacollection: null,
                options: {
                    maintainAspectRatio: false,
                    tooltips: {
                        mode: 'index',
                        intersect: 'false'
                    },
                    legend: {
                        display: false,
                    },
                    scales: {
                        yAxes: [{
                            id:  'Response Time',
                            ticks: {
                                beginAtZero: true,
                            },
                            display: true,
                            type: 'linear',
                        }],
                        xAxes: [{
                            display: true,
                            type: 'category',
                            labels: this.current_time,
                            bounds: 'data',
                        }]
                    }
                }
            }
        },
        mounted() {
            this.fillData();
        },
        computed: {
        },
        methods: {
            async fillData() {
                const table_data = await axios.get(settings.assets_url + "/site", {
                    params: {
                        id: this.site_params.id,
                        url: this.site_params.url,
                        interval: this.interval
                    }
                    })
                    .then((response) => {
                        this.data.push(response.data);
                        this.request_status.push(response.data.response_code);
                        this.request_time.push(response.data.response_time);
                        this.current_time.push(response.data.time);
                        console.log(this.data);
                    })
                    .catch(function (error) {
                        console.log(error)
                });
                this.datacollection = {
                    labels: this.current_time,
                    datasets: [{
                        name: 'Response Time: ',
                        yAxisID: 'Response Time',
                        pointRadius: 4,
                        data: this.request_time,
                        fill: true,
                        borderColor: 'grey',
                        backgroundColor: 'rgb(211,211,211)'
                    }],
                };
                console.log()
            },
            startInterval() {
                this.$int = setInterval(this.fillData, this.interval * 1000);
                console.log(this.interval);
                this.interval_btn = 'End interval';
                this.interval_fun = !this.interval_fun;
            },
            stopInterval() {
                clearInterval(this.$int);
                this.interval_btn = 'Start interval';
                this.interval_fun = !this.interval_fun;
            },
            clearChart() {
                this.datacollection.labels.pop();
                this.datacollection.datasets.forEach((dataset) => {
                    dataset.data.pop();
                });
                this.datacollection.update();
            }
        },
    }
</script>

<style scoped>
    .chartAreaWrapper {
        width: 100%;
        overflow-x: scroll;
    }
    v-row {
        margin-left: 10px;
    }


</style>